import React, { useEffect, useState } from "react";
import "./App.css";
import { GoogleLogin, GoogleLogout } from "react-google-login";
import Main from "./Main";
import AppLogo from "./MyComponent/app-logo.png";
import { auth, provider } from "./firebase";

function App() {
  const [name, setName] = useState("");
  const [email, setEmail] = useState("");
  const [url, setUrl] = useState("");
  const [isLoggedIn, setloggedIn] = useState(false);

  const responseGoogle = (response) => {
    setName(response.profileObj.name);
    setEmail(response.profileObj.email);
    setUrl(response.profileObj.imageUrl);
    setloggedIn(true);
    const rememberMe = true;
    localStorage.setItem("rememberMe", rememberMe);
    localStorage.setItem("user", rememberMe ? response.profileObj.email : "");
    localStorage.setItem(
      "username",
      rememberMe ? response.profileObj.name : ""
    );
    localStorage.setItem("url", rememberMe ? response.profileObj.imageUrl : "");
  };

  useEffect(() => {
    const rememberMe = localStorage.getItem("rememberMe") === "true";
    const user = rememberMe ? localStorage.getItem("user") : "";
    const username = rememberMe ? localStorage.getItem("username") : "";
    const imgUrl = rememberMe ? localStorage.getItem("url") : "";
    setName(username);
    setEmail(user);
    setUrl(imgUrl);
    if (rememberMe == true) {
      setloggedIn(true);
    } else {
      setloggedIn(false);
    }
  });

  const signIn = () => {
    // google login
    auth.signInWithPopup(provider).catch((error) => alert(error.message));
  };
  return (
    <>
      {isLoggedIn ? (
        <Main funloggedIn={setloggedIn} name={name} email={email} url={url} />
      ) : (
        <div className="container login-btn">
          <img className="app-logo" src={AppLogo} alt="" />
          <GoogleLogin
            clientId="1003818899851-l72n0ablk4ro0ronrgqjsggc2sml8dp4.apps.googleusercontent.com"
            buttonText="Login with Google"
            onSuccess={responseGoogle}
            cookiePolicy={"single_host_origin"}
          />
          {/* 
          <button className="btn btn-primary" onClick={signIn}>
            Sign In with Google
          </button> */}
        </div>
      )}
    </>
  );
}

export default App;
