import React, { useEffect, useState } from "react";
import Spm from "./MyComponent/subComp/SPM/Spm";
import Aplr from "./MyComponent/subComp/Aplr/Aplr";
import Ittool from "./MyComponent/subComp/ITTOOL/Ittool";
import Android from "./MyComponent/subComp/Android/Android";
import Adbms from "./MyComponent/subComp/ADBMS/Adbms";
import CoreJava from "./MyComponent/subComp/CoreJava/Java";
import ScrollToTop from "./MyComponent/ScrollToTop";
import Tutorial from "./MyComponent/Tutorial/Tutorial";
import { Route, Switch, useHistory } from "react-router-dom";
import Navbar from "./MyComponent/Navbar/Navbar";
import Subjects from "./MyComponent/Subjects/Subjects";

function Main(props) {
  const [NavTitle, setNavTitle] = useState("Classroom");
  const history = useHistory();
  const funloggedIn = props.funloggedIn;
  useEffect(() => {
    return history.listen((location) => {
      if (history.action === "POP") {
        setNavTitle("Classroom");
      }
    });
  }, [NavTitle]);

  return (
    <div className="main-div">
      <Navbar
        head={NavTitle}
        funloggedIn={funloggedIn}
        url={props.url}
        setNavTitle={setNavTitle}
      />
      <Switch>
        <Route exact path="/">
          <Subjects setNavTitle={setNavTitle} />
        </Route>
        <Route exact path="/ClassroomCode">
          <Subjects setNavTitle={setNavTitle} />
        </Route>
        <Route exact path="/ClassroomCode/spm">
          <ScrollToTop />
          <Spm />
        </Route>
        <Route exact path="/ClassroomCode/aplr">
          <ScrollToTop />
          <Aplr />
        </Route>
        <Route exact path="/ClassroomCode/ittool">
          <ScrollToTop />
          <Ittool />
        </Route>
        <Route exact path="/ClassroomCode/aand">
          <ScrollToTop />
          <Android />
        </Route>
        <Route exact path="/ClassroomCode/adbms">
          <ScrollToTop />
          <Adbms />
        </Route>
        <Route exact path="/ClassroomCode/corejava">
          <ScrollToTop />
          <CoreJava />
        </Route>
        <Route exact path="/ClassroomCode/tutorial">
          <ScrollToTop />
          <Tutorial />
        </Route>
      </Switch>
    </div>
  );
}

export default Main;
